html {
  --header-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

*, :after, :before {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
}

header {
  width: 100%;
  min-width: 1024px;
  margin: 0;
  padding: 0 50px;
  box-shadow: 0 2px 4px #0000000d;
}

header img {
  padding-top: 10px;
  position: absolute;
}

.nav {
  text-align: center;
  justify-content: end;
  height: 60px;
  margin: 0;
  display: flex;
}

.nav_list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav_item {
  margin-right: 20px;
  list-style: none;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: var(--header-color);
}

.is-active:after {
  content: "";
  background-color: var(--header-color);
  border-radius: 3px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--header-color);
}
